import React from "react";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";
import Main from './Pages/Main/main'
function App() {
 
  return (
    <Router>
   <div>
      <Switch>
        <Route path="/">
          <Main />
        </Route>
        <Route path="/users">
        <Main />
        </Route>
        <Route path="/">
        <Main />
        </Route>
      </Switch>
      </div>
  </Router>
  );
}

export default App;
