import React,{useState,useEffect} from 'react';
import { PDFReader,MobilePDFReader } from 'reactjs-pdf-reader';
import {Container,Row,Col} from 'react-grid-system'
function Certificate() {
    const [w, setW] = useState(window.innerWidth);
    const [h, setH] = useState(window.innerHeight);
    const [bar, setBar] = useState(()=>{
      if(w<480)
      return h-560+130;
      if(w<900)
      return h-560+100;
      if(w<1050)
      return h-560+70;
      if(w<1300)
      return h-560+40;
      else 
      return h-560;
    });
  
    window.addEventListener('resize',()=>{
     setW(window.innerWidth);
     setH(window.innerHeight);
     console.log(window.innerWidth);
      setBar(()=>{
        if(w<480)
        return h-560+130;
        if(w<900)
        return h-560+100;
        if(w<1050)
        return h-560+70;
        if(w<1300)
        return h-560+40;
        else 
        return h-560;
      });
    })
    return (
      <Container style={{color:"#D6D3CD"}}>
      <div className="App">
         <Row  style={{fontSize:72,paddingTop:200,paddingBottom:100}}>
         
          <Col xl={6} style={{fontFamily:'UnifrakturCook'}}>Machine Learning</Col>
         </Row>
         <Row>
<img src={require('../../assets/img/coursera-1.png')} style={{width:w}}></img>
</Row>
<Row justify="start" style={{fontSize:72,paddingTop:200,paddingBottom:100}}>
           <Col xl={1}></Col>
          <Col xl={12}  style={{fontFamily:'UnifrakturCook'}}>Introduction to TensorFlow for Artificial Intelligence, Machine Learning, and Deep Learning</Col>
         </Row>
         <Row>
<img src={require('../../assets/img/coursera2.png')} style={{width:w}}></img>
</Row>
      </div>
      </Container>
    );
  }
  
  export default Certificate;
  